@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
@media screen and (min-width: 900px) and (max-width: 1200px) {
  .content-wrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: stretch;
  }
  .container .about_list {
    flex-direction: column;
  }
  .about_list .about_item {
    padding-left: 0;
  }

  .skill_list .skill_item {
    padding-right: 70px;
  }
}

@media screen and (min-width: 768px) and (max-width: 900px) {
  .content-wrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
  }

  .container .about_title {
    font-size: 1.3rem;
  }

  .container .about_list {
    flex-direction: column;
  }

  .about_list .about_item {
    padding-left: 0;
  }

  .about_content {
    padding: 30px;
  }
}

@media screen and (max-width: 768px) {
  .container .about_title {
    font-size: 1.2rem;
  }

  .container .about_profile img {
    width: 230px;
    border-radius: 50%;
  }

  .container .about_list {
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }

  .about_title {
    text-align: center;
  }

  .about_list .about_item {
    width: 100%;
    padding: 0;
  }

  .about_list .about_content {
    width: 100%;
    text-align: center;
  }

  .about_item .skill_list {
    flex-direction: column;
  }

  .skill_list .skill_item {
    padding-right: 0;
    margin: 0 auto;
  }
}

.container {
  width: 100%;
  height: auto;
  padding-top: 50px;
  margin: 0 0 -40px 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: auto;
}

.about_title {
  text-align: center;
  font-size: 1.5rem;
  color: #3e3e3e;
  font-weight: 100;
}
.about-text {
  font-size: 1.4rem;
}

.about_profile img {
  width: 180px;
  border-radius: 50%;
}

.about_list {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.about_item {
  padding-left: 80px;
}

.about_content {
  text-align: left;
  padding-bottom: 20px;
}

.about_content p {
  font-size: 0.9rem;
  color: #3e3e3e;
}

.icon {
  margin-right: 10px;
}

.skill_list {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
}

.skill_item {
  padding-right: 20px;
}

.skill_item ul {
  padding: 0;
}
.skill_item li {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.skill_item p {
  text-align: center;
  font-size: 0.8rem;
  color: #3e3e3e;
  padding: 10px 0 0 10px;
}

@media (max-width: 768px) {
  #modal-root .Modal {
    width: 400px;
  }

  #modal-root .Modal .content p {
    padding: 10px 0px;
    font-size: 0.8rem;
    text-align: left;
    color: #3e3e3e;
  }
}

.Modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.16);
}

.Modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 550px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
}
.Modal > * {
  margin-left: 16px;
  margin-right: 16px;
}
.Modal > *:first-child {
  margin-top: 16px;
}
.Modal h2.title {
  font-weight: 600;
  color: #000;
  text-align: center;
}
.Modal .content {
  border-top: 1px solid #bebebe;
  margin-top: 16px;
}

.Modal .content p {
  padding: 10px;
  font-size: 0.9rem;
  text-align: left;
  color: #3e3e3e;
}

.content-subtitle {
  font-weight: bold;
  color: #000;
  margin-right: 10px;
}

.gitlink {
  font-style: italic;
  font-weight: 500;
  text-decoration: none;
}
.gitlink:hover {
  cursor: pointer;
}
.Modal .button-wrap {
  margin: 0;
  margin-top: 8px;
}
.Modal .button-wrap button {
  width: 100%;
  padding: 12px 0;
  border-radius: 0 0 10px 10px;
  background-color: #3e3e3e;
  font-size: 0.9rem;
  color: #fff;
  border: 0;
  cursor: pointer;
}
.Modal .button-wrap button:hover {
  background-color: #bebebe;
  outline: none;
}
.Modal .button-wrap button:active {
  background-color: #bebebe;
  outline: none;
}

@media screen and (min-width: 768px) and (max-width: 1330px) {
  .project_wrapper {
    padding-top: 0 !important;
  }
  .project_list {
    flex-direction: column !important;
  }
}
@media screen and (max-width: 768px) {
  .project_wrapper {
    padding-top: 0 !important;
  }
  .project_list {
    flex-direction: column !important;
  }
  .project_list .project__img:hover {
    width: 410px;
    transition: 0.3s;
    cursor: pointer;
  }
}

.project_container {
  width: 100%;
  height: auto;
  padding-top: 50px;
  /* header & footer height */
  margin-bottom: -40px;
  text-align: center;
}

.project_wrapper {
  padding-top: 200px;
}

.project_title {
  font-size: 1.5rem;
  font-weight: 100;
  letter-spacing: 0.1rem;
}
.project_list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 0;
}

.project__img {
  width: 380px;
  height: 210px;
  border-radius: 10px;
  box-shadow: 8px 8px 10px rgba(0, 0, 0, 0.2);
  margin: 10px;
}

.project__img:hover {
  width: 410px;
  height: 230px;
  transition: 0.3s;
  cursor: pointer;
}

/* Layout */
html,
body {
  font-family: "Poppins", "Noto Sans KR" sans-serif;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

header,
footer {
  user-select: none;
  -webkit-user-select: none;
  -webkit-user-drag: none;
  -ms-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
}
body {
  width: 100%;
  min-height: 100%;
}

#root,
.App {
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

